
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import Detail from "./propagandaInfo.vue";
export default {
components: {
Scroller,
Group,
Spinner,
Cell,
Detail
},
data() {
return {
propagandaArr: [],
pullupEnabled: true,
title: {
name: "propaganda",
flag: false,
backUrl: "/focus?jump=false"
},
status: {
pullupStatus: "default",
pulldownStatus: "default"
},
pullupConfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: ""
},
currpage: 0,
requestUrl: "",
popshow: false,
fbcshow: false,
query: {}
};
},
created() {
this.requestUrl = this.$myConfig.ip + ":" + this.$myConfig.port;
this.refresh();
},
mounted: function() {
window.setI18nValue = this.$setI18nValue; 
},
methods: {
closeDetail() {
this.popshow = false;
this.fbcshow = false;
},
SubCon(_id) {

let str = this.$i18n.locale;
/* if(str=='zh-CN'){
this.query=this.propagandaArr[_id].zh;
}else if(str=='en-US'){
this.query=this.propagandaArr[_id].en;
}else if(str=='pt-PT'){
this.query=this.propagandaArr[_id].pt;
} */

this.query = this.propagandaArr[_id];
if (this.query.detailUrl == "fbc") {

this.query.detailUrl = "static/detailIndex.html";

}
this.$router.push({path:'/propaganda/info',query: this.query});
},
loadMore() {
var _this = this;
setTimeout(() => {
_this.currpage++;
/* _this.refresh("1");

setTimeout(() => {
this.$refs.scroller.donePullup()
}, 10) */
}, 2000);
},
refreshload() {
this.refresh();
},
refresh(qtype) {
var _this = this;
if (qtype == undefined) {
_this.currpage = 0;
}
_this.$vux.loading.show({ text: _this.$t("loading") });

let qurl=window.g.get_sciencepopularizations
|| (_this.requestUrl + "sciencepopularization/sciencepopularizations");
var i18nstr = _this.$i18n.locale;
if (i18nstr != "") {
i18nstr = i18nstr.split("-")[0];
}

_this.$commonAjax(
"GET",
qurl,
{ language: i18nstr },
function(data) {
_this.popshow = false;
_this.$vux.loading.hide();
if (data.data != "") {
var datalist = JSON.parse(data.data);
if (datalist.length > 0) {
if (qtype == undefined) {
_this.propagandaArr = datalist;
var dataObj = {
detailUrl: "fbc",
publishtime: "2019-03-05 14:52:21",
title: _this.$t("StormSurge")
};
_this.propagandaArr.splice(0, 0, dataObj);
} else {
var data = datalist;
for (var i = 0; i < data.length; i++) {
_this.propagandaArr.push(data[i]);
}
}
} else {
if (qtype == undefined) {
_this.propagandaArr = [];
var dataObj = {
detailUrl: "fbc",
publishtime: "2019-03-05 14:52:21",
title: _this.$t("StormSurge")
};
_this.propagandaArr.splice(0, 0, dataObj);
}

}
}

},
function() {
_this.showBottomToast(_this.$t("requesterror"));
_this.propagandaArr = [];
_this.popshow = false;
/* ./static/detailIndex.html */
var dataObj = {
detailUrl: "fbc",
publishtime: "2019-03-05 14:52:21",
title: _this.$t("StormSurge")
};
_this.propagandaArr.splice(0, 0, dataObj);
_this.$vux.loading.hide();
},
true
);


this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown();

}, 10);
});
},
changePullupStatus(enabled) {
if (enabled) {
this.$refs.scroller.enablePullup();
this.pullupEnabled = true;
} else {
this.$refs.scroller.disablePullup();
this.pullupEnabled = false;
}
},
showBottomToast(val) {

this.$showBottomToast(val, "bottom");
},
jumpPhoneDial() {
try {
this.$jumpQuery();
} catch (e) {
console.log(e);
this.$router.push("/query/query");
}
}
}
};



